import Head from 'next/head'

import { Header, HeaderButton } from '../components/layout/Header';
import { Dropdown, DropdownItem } from '../components/MultilevelMenu';
import { Container } from '../components/layout/Container';
import { PostList } from '../components/PostList';
import { AccountPicker } from '../components/AccountPicker';
import { DummyPost } from "../components/DummyPost";
import { PostForm } from "../components/PostForm";

import { MdSettings as SettingsIcon, MdSwapHoriz } from "react-icons/md";
import { FaUser as UserIcon, FaPlus as PlusIcon } from "react-icons/fa";
import { AiOutlineUserSwitch } from "react-icons/ai";

import { IdentityProvider } from "../hooks/identity";
import { ContextMenu } from "../components/layout/ContextMenu";
import { EyeProvider } from '../components/layout/EyeOfJudgement';
import { useEffect, useState } from 'react';
import { PushSubToggle } from '../components/PushSubToggle';

export default function Home() {
    var [plsRerender, setRerender] =useState(Math.random());
    function rerender(id) {
        console.log("rerender")
        if(id) {
            return setRerender(id);
        }
        setRerender(Math.random());
    }
    useEffect(() =>{
        if("serviceWorker" in navigator) {
            navigator.serviceWorker.register(process.env.assetPrefix+"/servicework.js").then(async reg => {
                console.log("registered -> ", reg);
    
                //sw message
                navigator.serviceWorker.addEventListener("message", (ev) => {
                    //console.log("bruh");
                });
            })
            .catch(err => {
                console.log("error -> ",err)
            });
        }
    }, []);

    return (
        <EyeProvider>
            <Head>
                <title>Civil Discussion</title>
            </Head>

            
            <IdentityProvider>
                <ContextMenu style={{backgroundColor: "#181a1b", height: "100vh", width:"100vw"}}>
                    <Header title="Discussion" rerenderFunc={rerender}>    
                        <HeaderButton icon={<PlusIcon/>} drawer={
                            <PostForm/>
                        }></HeaderButton>
                        <HeaderButton icon={<UserIcon/>}>
                            <Dropdown>
                                <DropdownItem text="Profile"/>
                                <DropdownItem leftIcon={<MdSwapHoriz/>} text="Switch Acount">
                                    <AccountPicker/>
                                </DropdownItem>
                                <PushSubToggle></PushSubToggle>
                                <DropdownItem leftIcon={<SettingsIcon/>} text="Account Settings"/>
                            </Dropdown>
                        </HeaderButton>
                        <HeaderButton icon={<SettingsIcon/>}>
                            <Dropdown>
                                <DropdownItem text="Dark Theme"/>
                                <DropdownItem text="Privacy Policy" link={process.env.assetPrefix+"/privacy"}/>
                            </Dropdown>
                        </HeaderButton>
                    </Header>
                    <Container>
                        <PostList rerender={plsRerender} rerenderFunc={rerender}></PostList>
                    </Container>
                </ContextMenu>
            </IdentityProvider>
        </EyeProvider>
    )
}



 